:root {
  --pad-sm: 2em;
  --pad-md: 4em;
  --pad-lg: 6em;
  --font-main: "Work Sans", sans-serif;
  --font-mono: "IBM Plex Mono", "Monaco", monospace;
  --color-body-bg: #312626;
  --color-body-bg-donut: #120c10;
  --color-body-bg-blend: #090909;
  --cursor-color: #fff;
}

*, :before, :after {
  box-sizing: border-box;
}

html, body {
  background-color: var(--color-body-bg);
  color: #fff;
  font-family: var(--font-main);
  margin: 0;
  padding: 0;
  animation: .3s .1s both fade-in;
}

body.donut {
  background-color: var(--color-body-bg-donut);
}

body.blend {
  background-color: var(--color-body-bg-blend);
}

@keyframes fade-in {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

a {
  color: #fff;
  font-weight: 600;
  text-decoration: none;
  transition: all .5s;
}

p a {
  border-bottom: 1px solid #ffffffb3;
}

p a:hover {
  color: #ffffff80;
  border-bottom-color: #fff;
}

.section {
  width: 80%;
  max-width: 32em;
  margin: 0 auto 1em;
  padding: 4em 0;
  font-weight: 300;
  line-height: 1.7;
}

.page-title {
  margin-bottom: .25em;
  font-size: 2.5em;
  font-weight: 800;
  line-height: 1.4;
}

h3 {
  margin: 2em 0 1.5em;
}

ul {
  margin: 2em 0;
  padding-left: 1em;
}

ul li {
  padding-bottom: 1.5em;
  padding-left: 0;
}

.app-nav a.is-active {
  color: #aaa;
}

.app-header {
  z-index: 9;
  width: 100%;
  padding: 1em;
  position: fixed;
  top: 0;
}

.app-header__grid {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.app-header__brand {
  color: #fff;
  font-size: .6em;
  font-family: var(--font-mono);
  letter-spacing: .1em;
  text-transform: uppercase;
  cursor: pointer;
}

.app-header__icon .icon-github {
  height: 1.5em;
}

.app-header__icon .icon-github path {
  fill: var(--color-alpha, #fff);
}

.app-footer {
  padding: var(--pad-sm) 0;
  text-align: center;
  border-top: 1px solid #ffffff1a;
}

.app-footer__icon {
  margin-bottom: 1em;
  font-size: 1em;
  font-style: normal;
  display: block;
}

.app-footer__text {
  font-family: var(--font-mono);
  letter-spacing: .1em;
  text-transform: uppercase;
  margin-bottom: 0;
  font-size: .65em;
}

#blend-cursor-inner {
  background-color: pink;
}

